


html {
  height: 100%;
}

#root {
  height: 100%;
  width: 100%;
  --background-color: #efe9f4;
  --slate:  #6f8695;
  --light-slate: #aabac5;
  --slate-transparent: rgba(111,134,149,.25);
  --menu-color: #03312e;
  --licorice: #18020c;
  --text: #18020c;
  --light-text: #4ce0b3;
  --content-width: 87.5%;
  --var3:0;
  color: var(--text);
  background-color: var(--background-color);
}




.App {
  height: 100%;
}

body {
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button {
  background: none;
  border: none;
  font-size: 1rem;
  height: 2rem;
  color: var(--text)
}

button + button {
  border-left: 2.5px var(--slate-transparent) solid;
}

button:hover {
  color: var(--text);
  cursor: pointer
}