#main {
    display: flex;
    flex-direction: row;
    height: 100%;
}



.content {
    width: var(--content-width);
    height: auto;
    padding: 2.5%;
    display: inline-block;
    margin-left: calc(100% - var(--content-width));
    text-align: left;
    background-color: var(--background-color);
    
}



.profile {
    
    width: 20%;
    height: 100vh;
}
