#menu {
    background: var(--menu-color);
    width: calc(100% - var(--content-width));
    
    height: 100vh;
    position:fixed;
    border-radius: 0 4vh 4vh 0px;
}

#menu nav ul li {
    list-style: none;
    text-align: left;
    padding: 10px 0px;
    height: 2.5em;
    
}

nav ul li a {
    text-decoration: none;
    color: var(--light-text);
    font-size: 1.5rem;
}

nav ul li a::before {
    display: none;
}

@media (max-width: 1280px) {
    #menu {
        width: 100px;
    }

    #menu nav ul li a {
        font-size: .75rem;
        
    }

    #menu nav ul li a::before {
        display: initial
    }


    #menu nav ul li{
        
        height: 1rem;
    }

    #menu nav ul{
        padding-left: 15%;
    }
}


#profile {
    
    margin: 10% auto;
    width: 50%;
    height: auto;
    
    background: none;
    display: flex;
    flex-direction: row;
    justify-content: center;
    overflow: hidden;
}

#profile img {
    border-radius: 50%;
    cursor: pointer;
}

.activeLink {
    padding-left: 7.5px;
    
}

li:has(.activeLink) {
    
    border-right: 7.5px var(--light-text) solid;
}
button.navButton {
    color: var(--light-text);
    font-size: 1.5rem;
    height: 2rem;
    padding: 0;

}

button.navButton:hover {
    color: var(--slate)

}


