#cards {
    position:  relative;
    height: 100%;
}


#cards div#container {
    height: 100%;
}

#cards div#container div h2{
    margin: auto 0;
    
    height: auto;
}
#cards div#container div p {
    margin: auto 0;
    
}

#cards div#container div.content {
    height: 100%;
    display: flex;
    flex-direction: column;
    
    
    
}

div.content::-webkit-scrollbar-track {
    background-color: none;
    width: 0;
  } 

#cards div#container div.header {
    padding: 0 1.25%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.searchCards {
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    
}

.searchCards *  {
    margin: 1.25% 2.5%;
}

.cardContainer {
    width: 97.5%;
    
    margin: 0 auto 1.25%;
    overflow-y: scroll;
}

::-webkit-scrollbar {
    background-color: var(--background-color);
    width: 7.5px
}

::-webkit-scrollbar-track {
    background: var(--background-color);
  }

::-webkit-scrollbar-thumb {
    background: var(--slate);
    border-radius: 30px;
}

.searchBar {
    display: flex;
    flex: column;
    justify-content: center;
}

form  div{
    display: block;
}
